import React, { useEffect } from 'react'
import Container from 'Shared/components/Container'
import { addLightBackground, removeLightBackground } from 'Shared/helpers/layout'
import './OlivPlusMaintenance.scss'
import { notFoundRedirects } from 'Shared/constants/Routes'
import { PrimaryButton } from 'Shared/components/Button'
import { Link } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'

function OlivPlusMaitenance (props) {
  const { t } = props

  useEffect(() => {
    addLightBackground()
  }, [])

  useEffect(() => {
    return () => {
      removeLightBackground()
    }
  }, [])

  return (
    <div className='oliv-plus-maitenance__container'>
      <Container>
        <span className='oliv-plus-maitenance__heading'>
          Hey there! Thanks so much for your interest!
        </span>
        <br />
        <br />
        <span className='oliv-plus-maitenance__description'>
          We are currently refining the experience offered by Oliv Plus, and thus it is currently unavailable for now. As soon as the new and improved Oliv Plus becomes available, we will be sure to let you know.
        </span>
        <br />
        <br />
        <span className='oliv-plus-maitenance__description'>
          Happy Internship hunting.
        </span>
        <br />
        <br />
        <span className='oliv-plus-maitenance__description'>
          Kind Regards
        </span>
        <br />
        <br />
        <span className='oliv-plus-maitenance__description'>
          <i>The Oliv Team</i>
        </span>
        <br />
        <br />
        <Link to={notFoundRedirects.candidate}>
          <PrimaryButton>{t(`notFoundPage.ctaButton.candidate`)}</PrimaryButton>
        </Link>
        <br />
        <br />
      </Container>
    </div>
  )
}

export default withTranslations(OlivPlusMaitenance)



